import React, { useState } from "react";
import { ImCross } from "react-icons/im";

import "./Modal.css";

function Modal({ sources, show, setShow }) {
  const [showItem, setShowItem] = useState(sources[0].src);
  const handleScroll = e => {
    setShow(false);
  };

  document.body.onscroll = handleScroll;
  return (
    <div
      id='close'
      className='Modal-Popup'
      onClick={e => {
        e.stopPropagation();
        if (e.target.id === "close") {
          setShow(!show);
        }
      }}>
      <div className='Modal-wrapper'>
        <button className='Modal-exit' onClick={() => setShow(!show)}>
          <ImCross />
        </button>

        <div className='Modal-Content'>
          <div
            className='Modal-img'
            style={{ backgroundImage: `url(${showItem})` }}></div>
          <div className='Modal-Content-text'>
            <div className='Modal-Nav'>
              <ul className='Modal-nav-buttons'>
                {sources.map(item => (
                  <li onClick={() => setShowItem(item.src)}>{item.name}</li>
                ))}
              </ul>
            </div>
            <p>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Integer
              leo lacus, molestie a gravida quis, finibus et eros.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Modal;
